body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-around;
  display: flex;
}

.photo-card {
  width: 300px;
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin: 10px;
}

.photo-card img {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.info {
  text-align: center;
  padding: 10px;
}

.form-container {
  height: 50px;
  background-color: #87cefa;
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-form {
  padding: 0 auto;
}

.form-input {
  height: 30px;
  background: #f6f6f6;
  border: 1px solid #cecece;
  border-radius: 4px;
}

.submit-btn {
  height: 34px;
  color: #cecece;
  cursor: pointer;
  background: #4d4dff;
  border: 1px solid #cecece;
  font-weight: 600;
}

.submit-btn:hover, .submit-btn:focus {
  color: #fff;
}

